@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
header, footer, body, span, div, nav, svg, img, ul, li, h1, h2, h3, h4, h5, h6, a, p {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  text-decoration: none;
  list-style: none;
}

*, :before, :after {
  box-sizing: border-box;
}

:root {
  --orange: #f87719;
  --grey: #b7b7b7;
  --white: #fff;
  --black: #000;
  --background: #111;
  --secondary-text-grey: #f8f8f8;
  --secondary-black-grey: #282828;
  --grey-white-theme: #595959;
  color: var(--white);
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

.btn {
  cursor: pointer;
  border: none;
  border-radius: 74px;
  margin: 0;
  padding: 12px 24px;
  font-family: inherit;
  font-weight: 500;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.btn__orange-gradient {
  color: var(--background);
  background: linear-gradient(145deg, #ffc226 0%, #f84119 100%);
}

.btn.btn__orange-gradient:hover, .btn.btn__orange-gradient:active {
  background-color: var(--white);
  color: var(--orange);
  border: 1px solid var(--orange);
  background: none;
}

.btn__black {
  background: var(--background);
  color: var(--white);
  border: 1px solid var(--orange);
}

.btn.btn__black:hover, .btn.btn__black:active {
  background-color: var(--white);
  color: var(--orange);
  border-color: var(--background);
}

.btn__white {
  background: var(--white);
  color: var(--black);
  border: 1px solid #f84119;
}

.btn.btn__white:hover, .btn.btn__white:active {
  background-color: var(--black);
  color: var(--orange);
  border-color: var(--background);
}

.btn__small {
  font-size: 12px;
  line-height: 1.33333;
}

.btn__hero-1, .btn__hero-2 {
  width: 117px;
  height: 38px;
  font-family: var(--font-family);
  text-align: center;
  white-space: nowrap;
  border-radius: 74px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.btn__hero-1 {
  color: var(--background);
  background: linear-gradient(145deg, #ffc226 0%, #f84119 100%);
}

.btn__hero-1:hover, .btn__hero-1:active {
  color: var(--orange);
  background: var(--white);
}

.btn__hero-2 {
  color: var(--white);
  border: 1px solid var(--white);
  background: none;
}

.btn__hero-2:hover, .btn__hero-2:active {
  color: var(--background);
  background: var(--white);
}

.search {
  cursor: pointer;
  width: 48px;
  height: 48px;
  border: none;
  border-radius: 50%;
